@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Josefin+Sans&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}*{margin:0;outline:none}*{scrollbar-width:thin;scrollbar-color:#ff8a6671 #393a39}*::-webkit-scrollbar{width:12px}*::-webkit-scrollbar-track{background:#393a39}*::-webkit-scrollbar-thumb{background-color:#ff8a6671;border-radius:20px;border:3px solid #393a39}

:root{--primaryFont: "Lobster", cursive;--secondaryFont: "Josefin Sans", sans-serif;--primary: #2a2b2a;--primaryLight: #313231;--primaryLightDisabled: #313231f8;--accent: #ff8966;--accentRipple: #ff8a667a;--accentHighRipple: #ff8a66af;--accentMidRipple: #ff8a6691;--accentHighlight: rgba(255, 138, 102, 0.2);--accentLowHighlight: rgba(255, 138, 102, 0.15);--accentLowHighlightDisabled: rgba(255, 138, 102, 0.05);--accentSecondary: #3B8EA5;--text: #918d7b;--textWhite: #d4d4d4;--textDisabled: #918d7b56;--borderColor: var(--accentRipple);--error: #a60000;--success: #00c400be}#App{background-color:#313231;background-color:var(--primaryLight);min-height:100vh;font-family:"Josefin Sans", sans-serif;font-family:var(--secondaryFont);-webkit-user-select:none;-ms-user-select:none;user-select:none;display:grid;grid-template-rows:70px auto 60px}#App #switchContainer{display:flex;align-items:flex-start;justify-content:center;padding:calc(20px + 3%) 0}

#navBar{background-color:var(--primary);display:flex;align-items:center;justify-content:space-between;padding:0 10px;border-bottom:1px solid var(--borderColor)}#navBar p{color:var(--accentHighRipple)}#navBar a{color:var(--accent);font-family:var(--primaryFont);font-size:2.5rem;font-weight:500;cursor:pointer;text-decoration:none}#navBar div{display:flex;align-items:center;justify-content:center;height:100%}#navBar div a{display:flex;align-items:center;position:relative;height:100%;padding:0 10px;color:var(--accent);font-family:var(--secondaryFont);font-size:1rem;text-decoration:none}#navBar div a::before{content:"";display:block;position:absolute;width:60%;height:3px;bottom:10%;right:50%;transform:translateX(50%);background-color:var(--accentRipple);opacity:0;transition:opacity 500ms}#navBar div a:hover::before{opacity:1}@media only screen and (max-width: 250px){#navBar a{font-size:1.5rem}#navBar div a{font-size:.8rem;padding:0 5px}}@media only screen and (max-width: 180px){#navBar{flex-direction:column}#navBar div a{font-size:.8rem;padding:0 15px}}

.icon{cursor:pointer}.icon *{fill:var(--accentRipple);transition:fill 200ms linear}.icon:hover *{fill:var(--accent)}

#field{position:relative;display:flex;align-items:center;justify-content:space-between;width:85%;min-height:35px;margin:5px 0;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;box-shadow:none;transition:box-shadow 400ms}#field:hover,#field:focus-within{box-shadow:0 0 2px var(--accent)}#field.invalid{box-shadow:0 0 2px var(--error)}#field.valid{box-shadow:0 0 2px var(--success)}#field input{height:100%;width:calc(100% - 15px);margin-left:10px;background-color:transparent;color:var(--text);border:0;font-family:var(--secondaryFont)}#field input::selection{background-color:var(--accentHighlight)}#field input.pass{width:calc(100% - 45px)}#field button{z-index:1;width:25px;height:25px;margin:0 5px 0 0;border:0;border-radius:50%;background-color:var(--primary);color:var(--text);padding:0;cursor:pointer;transition:box-shadow 400ms}#field button:focus{box-shadow:none}#field button:hover{box-shadow:0 0 2px var(--accent)}

#loginContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;width:90%;max-width:300px;height:350px;background-color:var(--primary);border-radius:15px;border:1px solid var(--borderColor)}#loginContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#loginContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;margin-top:10px}#loginContainer form #forgotPass{margin-top:5px;margin-right:7%;align-self:flex-end;font-size:.8rem}#loginContainer form #error{margin-top:5px;font-size:.7rem;color:var(--error);text-align:center}#loginContainer #separator{width:60%;height:2px;margin-bottom:15px;background-color:var(--accentHighlight)}#loginContainer a{color:var(--accentHighRipple);text-decoration:none;padding:4px 8px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#loginContainer a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#loginContainer form>button,#loginContainer>button{width:40%;height:35px;margin-top:20px;margin-bottom:15px;padding:0;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;transition:box-shadow 300ms}#loginContainer form>button:hover,#loginContainer>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#loginContainer form>button:disabled,#loginContainer>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#loginContainer>button{margin-top:0;width:50%}#loginContainer #join{font-size:.8rem;color:var(--text);text-align:center;justify-self:flex-end}

#joinContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;width:90%;max-width:300px;height:400px;background-color:var(--primary);border-radius:15px;border:1px solid var(--borderColor)}#joinContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#joinContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:75%}#joinContainer form #error{font-size:.7rem;color:var(--error);margin-top:5px}#joinContainer form>button{margin-top:20px;margin-bottom:15px;padding:0;width:40%;height:35px;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;transition:box-shadow 300ms}#joinContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#joinContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#joinContainer #login{margin-top:10px;font-size:.8rem;color:var(--text);text-align:center}#joinContainer #login a{padding:4px 8px;color:var(--accentHighRipple);text-decoration:none;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#joinContainer #login a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}@media only screen and (max-width: 250px){#joinContainer{width:95%;height:90%}}

#forgotPassContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;width:90%;max-width:320px;height:80%;max-height:300px;min-height:250px;background-color:var(--primary);border-radius:15px;border:1px solid var(--borderColor)}#forgotPassContainer form{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;width:100%;height:80%}#forgotPassContainer form h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#forgotPassContainer form #desc{font-size:.9rem;color:var(--text);margin-bottom:5px;text-align:center}#forgotPassContainer form #error{font-size:.7rem;color:var(--error);margin-top:5px;text-align:center}#forgotPassContainer form>button{width:40%;height:35px;margin-top:20px;margin-bottom:15px;padding:0;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;transition:box-shadow 300ms}#forgotPassContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#forgotPassContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#forgotPassContainer #login{margin-top:10px;font-size:.8rem;color:var(--text);text-align:center}#forgotPassContainer #login a{color:var(--accentHighRipple);text-decoration:none;padding:4px 8px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#forgotPassContainer #login a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}

#resetPassContainer{display:flex;flex-direction:column;align-items:center;justify-content:flex-end;width:90%;max-width:300px;height:80%;max-height:250px;min-height:200px;background-color:var(--primary);border-radius:15px;border:1px solid var(--borderColor)}#resetPassContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#resetPassContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:80%}#resetPassContainer form p{font-size:.7rem;color:var(--error);margin-top:5px}#resetPassContainer form>button{width:40%;height:35px;margin-top:20px;margin-bottom:15px;padding:0;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;transition:box-shadow 300ms}#resetPassContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#resetPassContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}

#mainApp{width:90%;height:80vh;display:grid;background-color:var(--primary);border:1px solid var(--borderColor);border-radius:15px;transition:grid-template-rows 300ms linear;transition:grid-template-rows 300ms linear, -ms-grid-rows 300ms linear}#mainApp #chatContainer{margin:5px 4px 5px 0;border-radius:15px 15px 0 0;overflow-y:auto;max-height:100%}#mainApp #chatContainer #message{display:flex;align-items:center;justify-content:flex-start;padding:5px 10px;color:var(--textWhite);word-break:break-all}#mainApp #chatContainer #message>p{opacity:0;margin:0 10px;word-break:normal;font-size:.75rem;transition:opacity 500ms 500ms}#mainApp #chatContainer #message:hover>p{opacity:1}#mainApp #chatContainer #message div{display:flex;flex-direction:column;border-radius:0 15px 15px 15px;background-color:#3f3f3f;padding:5px 10px}#mainApp #chatContainer #message div p{font-size:.7rem}#mainApp #chatContainer #message.mine{justify-content:flex-end}#mainApp #chatContainer #message.mine div{text-align:end;background-color:var(--accentSecondary);max-width:90%;border-radius:15px 0 15px 15px}#mainApp #chatContainer #message.mine:hover+p{opacity:1}#mainApp #senderContainer{display:grid;grid-template-columns:1fr 70px;align-items:center;justify-content:space-between;border-radius:0 0 15px 15px;border-top:1px solid var(--borderColor);overflow:hidden}#mainApp #senderContainer #senderField{height:100%;width:100%;display:grid;grid-template-columns:1fr 40px;align-items:center;justify-content:space-between;border-radius:0 15px 15px 15px;border-right:1px solid var(--borderColor);background-color:var(--primaryLight)}#mainApp #senderContainer #senderField>div{display:grid;grid-template-rows:40px 1fr;justify-content:space-between}#mainApp #senderContainer #senderField>div input{padding-left:10px;border:0;background-color:transparent;color:var(--text);font-family:var(--secondaryFont);font-size:1.1rem}#mainApp #senderContainer #senderField>div div{display:flex;flex-wrap:wrap;height:100px;opacity:1;border-top:1px solid var(--accentRipple);overflow-y:scroll;transition:height 300ms linear, opacity 300ms linear}#mainApp #senderContainer #senderField>div div.hidden{height:0;opacity:0}#mainApp #senderContainer #senderField>div div span{margin:2px;font-size:1.3rem;cursor:pointer}#mainApp #senderContainer #senderField button svg{fill:var(--accentRipple);transition:fill 250ms linear}#mainApp #senderContainer #senderField button:hover svg{fill:var(--accent)}#mainApp #senderContainer button{display:flex;align-items:center;justify-content:center;background-color:transparent;border:0;padding:0;height:100%;cursor:pointer}#mainApp #senderContainer button svg{fill:var(--accentRipple);transition:fill 250ms linear}#mainApp #senderContainer button:hover svg{fill:var(--accent)}@media only screen and (max-width: 350px){#senderContainer input{width:90%}}

#policy{margin:0 5% 40px 5%;color:var(--text)}#policy a{color:var(--accentHighRipple);font-size:.9rem;text-decoration:none;padding:2px 6px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#policy a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#policy h1{margin-top:30px}#policy h2{margin-top:20px}#policy h3{margin-top:10px}#policy h1,#policy h2,#policy h3{margin-bottom:5px}

#footer{display:flex;align-items:center;justify-content:center;background-color:var(--primary);border-top:1px solid var(--borderColor)}#footer a{color:var(--accentHighRipple);font-size:.8rem;text-decoration:none;padding:5px 5px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#footer a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#footer #contacts{display:flex;align-items:center;justify-content:center}#footer #separator{width:2px;height:50%;background-color:var(--accentHighlight);border-radius:20px;margin:0 15px}

#loginGuestContainer{display:flex;flex-direction:column;align-items:center;justify-content:center;width:90%;max-width:300px;height:200px;background-color:var(--primary);border-radius:15px;border:1px solid var(--borderColor)}#loginGuestContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#loginGuestContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;margin-top:10px}#loginGuestContainer form #forgotPass{margin-top:5px;margin-right:7%;align-self:flex-end;font-size:.8rem}#loginGuestContainer form #error{margin-top:5px;font-size:.7rem;color:var(--error);text-align:center}#loginGuestContainer #separator{width:60%;height:2px;margin-bottom:15px;background-color:var(--accentHighlight)}#loginGuestContainer a{color:var(--accentHighRipple);text-decoration:none;padding:4px 8px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#loginGuestContainer a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#loginGuestContainer form>button,#loginGuestContainer>button{width:40%;height:35px;margin-top:20px;margin-bottom:15px;padding:0;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;transition:box-shadow 300ms}#loginGuestContainer form>button:hover,#loginGuestContainer>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#loginGuestContainer form>button:disabled,#loginGuestContainer>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#loginGuestContainer>button{margin-top:0;width:50%}#loginGuestContainer #join{font-size:.8rem;color:var(--text);text-align:center;justify-self:flex-end}

